import UuLoading from './loading'
import UuButton from './button'
import UuNotification from './notification'

const components = [
  UuLoading,
  UuButton,
  UuNotification
]

const install = (Vue) => {
  if (install.installed) return
  components.map(component => Vue.component(component.name, component))
  install.installed = true
}

if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  install,
  ...components
}